<template>
  <div v-if="state.host">
    <section>
      <h1 class="page-header">Buckets</h1>

      <div class="mt-2">
        <div class="bg-white table-responsive border border-top-0 rounded">
          <table class="table table-sm">
            <thead>
              <tr>
                <th scope="col" class="p-2" width="30%">Name</th>
                <th scope="col" class="p-2" width="50%">Created</th>
                <th scope="col" width="10%"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in state.host.buckets" :key="index">
                <th scope="row">{{ item.name }}</th>
                <td>{{ item.creationDate | date_format }}</td>
                <td class="text-right">
                  <router-link
                    :to="`/host/${state.host.name}/${item.name}`"
                    class="btn btn-xxs btn-outline-success"
                  >Open</router-link>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import CryptoAES from 'crypto-js/aes'
import CryptoUTF8 from 'crypto-js/enc-utf8'
export default {
  props: ['state'],
  watch: {
    'state.host': async function (value) {
      if (value) await this.listBuckets()
    }
  },
  async mounted() {
    if (this.state.host.client) await this.listBuckets()
  },
  methods: {
    async listBuckets() {
      if (!this.state.host.client) return
      console.log('buckets:listBuckets', this.state.host)
      // this.$set(host, 'bucket', this.bucket)
      // this.$set(host, 'prefix', this.prefix)

      try {
        this.$set(this.state.host, 'buckets', await this.state.host.client.listBuckets())
      } catch (e) {
        this.$set(this.state.host, 'buckets', [])
        this.$set(this.state.host, 'error', e.message)
      }

      this.$set(this.state, 'loading', false)

    //   // encrypt and store into local storage
    //   let state = JSON.parse(JSON.stringify(this.state))
    //   delete state.password
    //   delete state.host
    //   localStorage.state = CryptoAES.encrypt(JSON.stringify(state), this.state.password).toString()
    }
  }
}
</script>

