<template>
  <article class="content container-fluid" v-if="state.authenticated && state.host">
    <nav aria-label="breadcrumb">
      <div class="breadcrumb mt-3 d-flex justify-content-between align-items-center">
        <ol class="breadcrumb mb-0 p-0">
          <li class="breadcrumb-item">
            <a href="javascript:void(0)" @click="$router.push('/')">Hosts</a>
          </li>
          <li v-if="state.host && state.host.name" :class="['breadcrumb-item']" aria-current="page">
            <a
              href="javascript:void(0)"
              v-if="state.host && state.host.name && bucket"
              @click="$router.push(`/host/${state.host.name}`)"
            >{{ state.host.name }}</a>
            <span v-else>{{ state.host.name }}</span>
          </li>
          <li
            :class="['breadcrumb-item']"
            aria-current="page"
            v-if="state.host && state.host.name && bucket"
          >
            <a
              href="javascript:void(0)"
              v-if="state.host && state.host.name && !bucket && prefix"
              @click="$router.push(`/host/${state.host.name}/${bucket}`)"
            >{{ bucket }}</a>
            <span v-else>{{ bucket }}</span>
          </li>
          <li
            :class="['breadcrumb-item']"
            aria-current="page"
            v-if="state.host && state.host.name && bucket && prefix"
          >{{ prefix }}</li>
        </ol>
      </div>
    </nav>

    <alert :alert="alert"></alert>

    <template v-if="state.host.name">
      <host-buckets v-if="!state.host.bucket" :state="state"></host-buckets>
      <host-objects v-else :state="state"></host-objects>
    </template>
  </article>
</template>

<script>
import CryptoAES from 'crypto-js/aes'
import CryptoUTF8 from 'crypto-js/enc-utf8'
import hostBuckets from '../components/host/buckets'
import hostObjects from '../components/host/objects'

export default {
  name: "host",
  props: ['state', 'host_key', 'bucket', 'prefix'],
  components: {
    hostBuckets,
    hostObjects
  },
  data: function () {
    return {
      alert: {}
    }
  },
  watch: {
    host_key: async function (value) {
      console.log('host: watcher: host_key', value)
      if (value) {
        await this.setHost(this.state.hosts[value] || {})
      }
    },
    bucket: async function (value) {
      console.log('host: watcher: bucket', value)
      this.$set(this.state.host, 'bucket', value || '')
      if (value && this.state.host) {
        await this.setHost(this.state.host || {})
      }
    },
    'state.host': function (value) {
      console.log('host: watcher: state.host', value)
      if (value.error) {
        this.alert = {
          type: 'alert-danger',
          message: value.error
        }
      } else {
        this.alert = {}
      }
    }
  },
  beforeMount() {
    if (!this.state.authenticated) {
      this.$router.push('/login')
    }
  },
  async mounted() {
    await this.setHost(this.state.hosts[this.host_key])
  },
  methods: {
    async setHost(host) {
      console.log('host:setHost', host)

      this.alert = {}

      host = {
        name: this.host_key,
        hostname: '',
        url: '',
        port: 9000,
        useSSL: false,
        error: false,
        buckets: [],
        bucket: this.bucket || '',
        prefix: this.prefix || '',
        ...host
      }

      let h = new URL(host.url)

      host.hostname = h.hostname
      host.port = parseInt(h.port, 10)
      host.useSSL = h.protocol === 'https:'

      try {
        host.client = new minio.Client({
          endPoint: host.hostname,
          port: host.port,
          useSSL: host.useSSL,
          accessKey: host.accessKey,
          secretKey: host.secretKey
        })
      } catch (e) {
        host.error = e.message
      }

      //
      this.$set(this.state.hosts, this.host_key, host)

      //
      this.$set(this.state, 'host', host)

      // // encrypt and store into local storage
      // let state = JSON.parse(JSON.stringify(this.state))
      // delete state.password
      // delete state.host
      // console.log('pre store', state)
      // localStorage.state = CryptoAES.encrypt(JSON.stringify(state), this.state.password).toString()

      //
      //await this.listBuckets()
    },
    // async listBuckets() {
    //   if (!this.state.host) return

    //   console.log('host:listBuckets', this.state.host)
    //   // this.$set(host, 'bucket', this.bucket)
    //   // this.$set(host, 'prefix', this.prefix)
    //   console.log(typeof this.state.host.client.listBuckets)
    //   try {
    //     this.state.host.buckets = await this.state.host.client.listBuckets()
    //   } catch (e) {
    //     console.error(e)
    //     this.state.host.buckets = []
    //     this.state.host.error = e.message
    //   }

    //   // // encrypt and store into local storage
    //   // let state = JSON.parse(JSON.stringify(this.state))
    //   // delete state.password
    //   // delete state.host
    //   // localStorage.state = CryptoAES.encrypt(JSON.stringify(state), this.state.password).toString()
    // }
  }
}
</script>

