<template>
  <div class="card sticky" v-if="state.host && state.host.bucket">
    <div class="card-header">
      <div class="row no-gutters">
        <div class="card-title col-8">
          Objects ({{ state.host.bucket }}{{ state.host.prefix ? '/' + state.host.prefix : '' }})<br>
          <small class="text-muted">Total {{ paging.totalItems }} Items, Size: {{ totalBytes | formatBytes }}</small>
        </div>
        <div class="input-group input-group-sm col-4">
          <input class="form-control" v-model="objectFilter" placeholder="Filter objects..." />
        </div>
      </div>
    </div>
    <ul class="list-unstyled mb-0">
      <li class="media p-2 border-bottom" v-if="state.host.prefix">
        <div class="media-body" style="display: inline-grid">
          <div class="font-weight-bold mt-0 mb-1 text-truncate">
            <a href="javascript:void(0)" @click="backPrefix(state.host.prefix)">../ (go back)</a>
          </div>
        </div>
      </li>
      <li class="media p-2" v-if="filteredObjects.length === 0">
        <div class="media-body" style="display: inline-grid">
          <div class="text-truncate">No files.</div>
        </div>
      </li>
      <li
        class="media border-bottom p-2"
        v-for="item in filteredObjects"
        :key="item.name + item.prefix"
      >
        <template v-if="isImage(item.name)">
          <img
            class="mr-3 mt-1"
            :load="getImage(item)"
            :src="item.src || ''"
            alt
            style="width:120px; border-radius:5px"
          />
        </template>
        <template v-if="item.prefix">
          <div class="media-body" style="display: inline-grid">
            <div class="font-weight-bold mt-0 mb-1 text-truncate">
              <a href="javascript:void(0)" @click="setPrefix(item.prefix)">{{ item.prefix }}</a>
            </div>
          </div>
        </template>
        <template v-else>
          <div class="media-body" style="display: inline-grid">
            <div class="font-weight-bold mt-0 mb-1 text-truncate">{{ item.name }}</div>
            <small>
              <strong>Last Modified:</strong>
              <span class="text-muted nowrap">{{ item.lastModified | date_format }}</span>
            </small>
            <small>
              <strong>Type:</strong>
              <span class="text-muted nowrap">{{ item.name | mime }}</span>
            </small>
            <small>
              <strong>Size:</strong>
              <span class="text-muted nowrap">{{ item.size | formatBytes }}</span>
            </small>
          </div>
          <button type="button" class="btn btn-xxs btn-dark mr-2" @click="editItem(item)">
            <fa icon="pencil-alt"></fa>
          </button>
          <button
            type="button"
            class="btn btn-xxs btn-danger"
            @click="deleteItem(item)"
            :disabled="item.profile && item.profile.id === 0"
          >
            <fa icon="times"></fa>
          </button>
        </template>
      </li>
    </ul>
    <div class="p-2" style="background-color: #fafafa;" v-show="filteredObjects.length > 0">
      <ul class="pagination" style="justify-content: center" v-if="objects.length > 0">
        <li class="page-item pagination-prev" :class="{'disabled': paging.page == 1 }">
          <a class="page-link" href="javascript:void(0)" @click="() => { paging.back() }">
            <span>&laquo;</span>
            <span class="sr-only">Previous</span>
          </a>
        </li>
        <template v-for="pageNumber in paging.pages">
          <li
            class="page-item"
            v-if="Math.abs(pageNumber - paging.page) < 5 || pageNumber === paging.pages.length || pageNumber === 1"
            :class="{
                active: paging.page === pageNumber,
                last: (pageNumber === paging.pages.length && Math.abs(pageNumber - paging.page) > 5),
                first:(pageNumber === 1 && Math.abs(pageNumber - paging.page) > 5)
              }"
            @click="() => { paging.page = pageNumber }"
            :key="`pn-${pageNumber}`"
          >
            <a class="page-link" href="javascript:void(0)">{{ pageNumber }}</a>
          </li>
        </template>
        <li
          class="page-item pagination-next"
          :class="{'disabled': paging.page === paging.pages.length}"
        >
          <a class="page-link" href="javascript:void(0)" @click="() => { paging.next() }">
            <span>&raquo;</span>
            <span class="sr-only">Next</span>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import CryptoAES from 'crypto-js/aes'
import CryptoUTF8 from 'crypto-js/enc-utf8'
const mime = require('mime-types')

export default {
  props: ['state'],
  data: function () {
    return {
      alert: {},
      host: {},
      paging: {
        page: 1,
        pages: [],
        perPage: 10,
        totalItems: 0,
        totalItemsFiltered: 0,
        next: function () {
          this.page++
        },
        back: function () {
          --this.page
        }
      },
      objects: [],
      bucketFilter: '',
      objectFilter: '',
    }
  },
  watch: {
    'state.host.bucket': async function (newVal) {
      console.log('bucket: listObjects', newVal)
      await this.listObjects(newVal, '')
    },
    'state.host.prefix': async function (newVal) {
      console.log('prefix: listObjects', newVal)
      await this.listObjects(this.state.host.bucket, newVal)
    },
    objects: function () {
      //
      this.paging.pages = []
      this.paging.totalItems = this.objects.length
      //
      for (let i = 1; i <= Math.ceil(this.paging.totalItemsFiltered = this.objects.filter(item => {
        item.bucket = this.state.host.bucket
        return (item.prefix) || item.name && item.name.toLowerCase().indexOf(this.objectFilter.toLowerCase()) > -1
      }).length / this.paging.perPage); i++) {
        this.paging.pages.push(i)
      }
    },
    objectFilter: function () {
      this.paging.page = 1
    }
  },
  computed: {
    filteredObjects() {
      let objects = this.objects.filter(item => {
        item.bucket = this.state.host.bucket
        return (item.prefix) || item.name && item.name.toLowerCase().indexOf(this.objectFilter.toLowerCase()) > -1
      })
      return this.paging.page != -1 ? this.paginate(objects, this.paging.perPage, this.paging.page) : objects
    },
    totalBytes() {
      return this.objects.reduce((accu, curr) => accu = accu + curr.size, 0)
    }
  },
  filters: {
    mime: function (value) {
      return mime.lookup(value) || '-'
    },
    formatBytes(bytes) {
      if (bytes === 0) return '0 Bytes'

      const i = Math.floor(Math.log(bytes) / Math.log(1024))
      return parseFloat((bytes / Math.pow(1024, i)).toFixed(2)) + ' ' + [
        'Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'
      ][i]
    }
  },
  async mounted() {
    await this.listObjects(this.state.host.bucket)
  },
  methods: {
    paginate(array, page_size, page_number) {
      --page_number
      return array.slice(page_number * page_size, (page_number + 1) * page_size)
    },
    isImage(value) {
      if (!value) return false
      return ['jpg', 'jpeg', 'png', 'gif', 'svg', 'webp', 'bmp'].includes(value.split('.').pop().toLowerCase())
    },
    isEditable(value) {
      if (!value) return false
      return ['jpg', 'jpeg', 'png', 'md', 'txt', 'js', 'php'].includes(value.split('.').pop().toLowerCase())
    },
    setPrefix(prefix) {
      this.state.host.prefix = prefix
    },
    backPrefix(path) {
      path = path.replace(/\/+$/, '').split('/')
      path.pop()
      let newPath = path.join('/') + '/'
      this.setPrefix(newPath === '/' ? '' : newPath)
    },
    async listObjects() {
      try {

        this.objects = []

        const objectStream = await this.state.host.client.listObjectsV2(...arguments)

        objectStream.on('data', (obj) => {
          if (obj && (obj.name || obj.prefix)) {
            this.objects.push(obj)
          }
        })

        objectStream.on('error', (e) => {
          this.host.error = e.message
          this.alert = {
            type: 'alert-danger',
            message: e.message
          }
        })

        objectStream.on('end', () => {

        })

      } catch (e) {
        this.host.error = e.message
        this.alert = {
          type: 'alert-danger',
          message: e.message
        }
      }
    },
    async getImage(item) {
      if (item.src) return
      console.log('getImage', item)
      this.state.host.client.getObject(item.bucket, item.name, (err, dataStream) => {
        if (err) return console.error(err)

        let chunks = []
        dataStream.on('data', chunk => chunks.push(chunk))

        dataStream.on('end', () => {
          let data = escape(Buffer.concat(chunks || []).toString('base64'))
          let mimetype = mime.lookup(item.name)

          this.$set(item, 'src', `data:${mimetype};base64,${data}`)
        })
      })
    },
    open(item) {
      this.state.host.client.getObject(this.host.bucket, item.name, function (err, dataStream) {
        if (err) return console.error(err)

        let chunks = []
        dataStream.on('data', function (chunk) {
          chunks.push(chunk)
        })

        dataStream.on('end', function () {

          let data = escape(Buffer.concat(chunks || []).toString('base64'))
          let mimetype = mime.lookup(item.name)

          let win = window.open()
          win.document.write(`
            <iframe
              src="data:${mimetype};base64,${data}"
              style="
                position: fixed;
                top: 0px;
                bottom: 0px;
                right: 0px;
                width: 100%;
                border: none;
                margin: 0;
                padding: 0;
                overflow: hidden;
                height: 100%;
            "></iframe>
          `)
          //win.focus()
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.ellips {
  max-width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>
